import { Component, OnInit, ViewChild } from '@angular/core';
import { RequirementService } from '../../_services/requirement.service';
import { ShortfallList } from '../../_models/ShortfallList';
import { AlertifyService } from '../../_services/alertify.service';
import { Subject } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { ActiveModel } from '../../_models/ActiveModel';
import { ActiveProcessor } from '../../_models/ActiveProcessor';

@Component({
  selector: 'app-short-fall',
  templateUrl: './short-fall.component.html',
  styleUrls: ['./short-fall.component.css']
})
export class ShortFallComponent implements OnInit {
  roleid: any =  Number(String(localStorage.getItem('RoleID')));
  items: ShortfallList[];
  activemodels: ActiveModel[];
  activeprocs: ActiveProcessor[];
  model: any = {};
  procmodel: any = {};
  @ViewChild('infoModal') public infoModal: ModalDirective;
  @ViewChild(DataTableDirective) dtElement: DataTableDirective;
  dtOptions: DataTables.Settings = {
    pagingType: 'full_numbers',
    pageLength: 25,
    retrieve: true,
    order: [[1, 'desc']]
};
  dtInstance: DataTables.Api;
  isDtInitialized: boolean = false;
  dtTrigger: Subject<ShortfallList> = new Subject();

  constructor(private requirementservice: RequirementService, private loadspecsService: LoadspecsService,
                      private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.loadOrders();
    this.loadActiveModels();
    this.loadActiveprocs();
  }
  loadOrders() {
    return this.requirementservice.getShortFalls().subscribe((items: ShortfallList[]) => {
      this.items = items;
      console.log(this.items);
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });

  }
  loadActiveModels() {
    return this.loadspecsService.loadActiveModels(1).subscribe((activemodels: ActiveModel[]) => {
      this.activemodels = activemodels;
      console.log(this.activemodels);
    });
  }
  loadActiveprocs() {
    return this.loadspecsService.loadActiveProc(1).subscribe((activeprocs: ActiveProcessor[]) => {
      this.activeprocs = activeprocs;
    }, error => {
      console.log(error.error);
    });
  }
  activateModel(event, id) {
    this.model.user_id = Number(localStorage.getItem('UserId'));
    this.model.model_id = id;
    this.model.s_type = 1;
    this.model.status = 0;
    if (event.target.checked) {
      this.model.status = 1;
    }
    this.requirementservice.activateModel(this.model).subscribe(() => {
      this.loadOrders();
    }, error => {
      this.alertify.error(error);
    });
  }
  activateProc(event, id) {
    this.procmodel.user_id = Number(localStorage.getItem('UserId'));
    this.procmodel.proc_id = id;
    this.procmodel.s_type = 1;
    this.procmodel.status = 0;
    if (event.target.checked) {
      this.procmodel.status = 1;
    }
    this.requirementservice.activateProc(this.procmodel).subscribe(() => {
      this.loadOrders();
    }, error => {
      this.alertify.error(error);
    });
  }
  ResetModel() {
    this.model.user_id = Number(localStorage.getItem('UserId'));
    this.model.model_id = 0;
    this.model.s_type = 1;
    this.model.status = 3;
    this.requirementservice.activateModel(this.model).subscribe(() => {
      this.loadOrders();
      this.loadActiveModels();
    }, error => {
      this.alertify.error(error);
    });
  }
  ResetProc() {
    this.procmodel.user_id = Number(localStorage.getItem('UserId'));
    this.procmodel.proc_id = 0;
    this.procmodel.s_type = 1;
    this.procmodel.status = 3;
    this.requirementservice.activateProc(this.procmodel).subscribe(() => {
      this.loadOrders();
      this.loadActiveprocs();
    }, error => {
      this.alertify.error(error);
    });
  }

}
